import { erf, sqrt } from "mathjs";
import drawResult from "@/misc/genericCalc.js";
import variables from "@/misc/variables.js";

export default {
  data() {
    return {
      param_n: variables.inputDefaultValue,
      param_p: variables.inputDefaultValue,
      param_k1: variables.inputDefaultValue,
      param_k2: variables.inputDefaultValue,
      result: variables.outputTip,
      classObj: {}
    };
  },
  methods: {
    compute() {
      drawResult(this, this.lapalsInt, [
        +this.param_n,
        +this.param_p,
        +this.param_k1,
        +this.param_k2
      ]);
    },
    lapalsInt(n, p, k1, k2) {
      const x = k => (k - n * p) / sqrt(n * p * (1 - p));
      const F = x => erf(x / sqrt(2)) / 2;
      return F(x(k2)) - F(x(k1));
    }
  }
};
